.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 1200px;
    grid-template-areas:
  "h h"
  "n c";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    /* grid-gap: 10px; */
}

.app-wrapper-content {
    grid-area: c;
    background-image: url("https://hsto.org/getpro/tmtm/megapost/ea2/1ce/cb0/ea21cecb0844b1f674db6a54af8ba9ca.png");
    padding: 25px;
    background-repeat: no-repeat;
    background-size: contain;
}
